
import React, { useState } from 'react';
import { useAuthState } from 'react-firebase-hooks/auth';
import { useCollection } from 'react-firebase-hooks/firestore';
import { collection, query, where } from 'firebase/firestore';
import { auth, db } from '../../lib/firebase';
import { moveItemToPantry, depleteItemFromPantry } from '../../lib/logic';
import { type ListItem, type PantryItem } from '../../types';

import { SamLayout } from '../../components/sam/SamLayout';
import { MorningBriefing } from '../../components/sam/MorningBriefing';
import { InventoryItem } from '../../components/sam/InventoryItem';

export const SamDashboard = () => {
    const [user] = useAuthState(auth);
    const [activeTab, setActiveTab] = useState<'home' | 'pantry' | 'chat' | 'setup'>('home');
    const [activeListTab, setActiveListTab] = useState<'stock' | 'shopping' | 'events'>('shopping');

    // --- Data Fetching ---
    // 1. Shopping List Items (Unbought)
    const [itemsSnapshot] = useCollection(
        user ? query(collection(db, 'items'), where('userId', '==', user.uid), where('isBought', '==', false)) : null
    );
    const shoppingItems = itemsSnapshot?.docs.map(d => ({ id: d.id, ...d.data() } as ListItem)) || [];

    // 2. Pantry Items (Stock)
    const [pantrySnapshot] = useCollection(
        user ? query(collection(db, 'pantry'), where('userId', '==', user.uid)) : null
    );
    const pantryItems = pantrySnapshot?.docs.map(d => ({ id: d.id, ...d.data() } as PantryItem)) || [];


    // --- Handlers ---
    const handleToggleShopping = async (item: ListItem) => {
        if (!user) return;
        // Move to Pantry
        // Using "Shopping List" as generic source name for now if not tracked
        await moveItemToPantry(user.uid, item, "Shopping List");
    };

    const handleToggleStock = async (item: PantryItem) => {
        if (!user) return;
        // If checked (in stock) -> Uncheck (deplete and add back to list)
        // Since we are viewing "Stock", this item IS in stock.
        // So clicking it means we are depleting it. (Removing from pantry, adding back to shopping list)
        await depleteItemFromPantry(user.uid, item, true);
    };

    return (
        <SamLayout activeTab={activeTab} onNavigate={(tab) => setActiveTab(tab)}>
            <MorningBriefing />

            {/* Main Scroll Content */}
            <div className="mt-4">
                <h1 className="font-serif text-[22px] mb-1">
                    {activeListTab === 'shopping' ? 'Shopping List' : activeListTab === 'stock' ? 'Pantry Stock' : 'Events'}
                </h1>
                <div className="text-[13px] text-[var(--text-secondary)] mb-4">
                    {activeListTab === 'shopping' ? 'Items to purchase' : activeListTab === 'stock' ? 'What you have at home' : 'Upcoming events'}
                </div>

                {/* Tabs Mobile */}
                <div className="flex gap-1 bg-[var(--bg-card)] p-1 rounded-full mb-6 max-w-sm mx-auto w-full">
                    <button
                        onClick={() => setActiveListTab('stock')}
                        className={`flex-1 text-center py-2 text-[13px] rounded-full transition-colors ${activeListTab === 'stock' ? 'bg-[var(--accent-olive)] text-white' : 'text-[var(--text-secondary)]'}`}
                    >
                        Stock
                    </button>
                    <button
                        onClick={() => setActiveListTab('shopping')}
                        className={`flex-1 text-center py-2 text-[13px] rounded-full transition-colors ${activeListTab === 'shopping' ? 'bg-[var(--accent-olive)] text-white' : 'text-[var(--text-secondary)]'}`}
                    >
                        Shopping
                    </button>
                    <button
                        onClick={() => setActiveListTab('events')}
                        className={`flex-1 text-center py-2 text-[13px] rounded-full transition-colors ${activeListTab === 'events' ? 'bg-[var(--accent-olive)] text-white' : 'text-[var(--text-secondary)]'}`}
                    >
                        Events
                    </button>
                </div>

                {/* Inventory List Content */}
                <div className="flex flex-col gap-2">

                    {activeListTab === 'shopping' && (
                        <>
                            {shoppingItems.length === 0 && (
                                <div className="text-center text-[var(--text-secondary)] py-8 text-sm italic">
                                    Your shopping list is empty!
                                </div>
                            )}
                            {shoppingItems.map(item => (
                                <InventoryItem
                                    key={item.id}
                                    name={item.name}
                                    avatarLabel={item.name.charAt(0).toUpperCase()}
                                    isChecked={false} // Shopping list items are unchecked (to be bought)
                                    onToggle={() => handleToggleShopping(item)}
                                // subtext={item.quantity > 1 ? `x${item.quantity}` : undefined}
                                />
                            ))}
                        </>
                    )}

                    {activeListTab === 'stock' && (
                        <>
                            {pantryItems.length === 0 && (
                                <div className="text-center text-[var(--text-secondary)] py-8 text-sm italic">
                                    Your pantry is empty.
                                </div>
                            )}
                            {pantryItems.map(item => (
                                <InventoryItem
                                    key={item.id}
                                    name={item.name}
                                    avatarLabel={item.name.charAt(0).toUpperCase()}
                                    avatarColor="#8A9A85" // Sage for stock
                                    isChecked={true} // Stock items are checked (in stock)
                                    onToggle={() => handleToggleStock(item)}
                                    subtext={`Qty: ${item.quantity}`}
                                />
                            ))}
                        </>
                    )}

                    {activeListTab === 'events' && (
                        <div className="text-center text-[var(--text-secondary)] py-8 text-sm italic">
                            No upcoming events.
                        </div>
                    )}
                </div>
            </div>
        </SamLayout>
    );
};
